import React, { Component } from 'react'
import Box from '@material-ui/core/Box';
//import App from './App';
//import Content from './Content';
import Header from './Header';
import Footer from './Footer';

export class Register extends Component {
    constructor() {
        super();
        this.state = {value: '',participate: ''};
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
      changeHandler = (event) => {
          this.setState({participate: event.target.value});
      }
    
      handleChange(event) {
        this.setState({value: event.target.value});
      }
      
    
      handleSubmit(event) {
        alert('You Sucessfully Register this course: ' + this.state.value +'\nHave a nice day!! Happy Coding :-)');
        event.preventDefault();
      }
      
    
      render() {
        return (
          <div>
          <Header/>
          <div class="body1">
            <body1>
                <pre>

                </pre>
                <h1><u>Welcome to Registration Page</u></h1>
                
                <div style={{ marginLeft: '35%', marginTop: '60px', width: '30%' }}>
    <Box color="white" bgcolor="black" p={1}>
      
    
    
                
                
            <h1>Hey,{this.state.participate}</h1>
          <form onSubmit={this.handleSubmit}>
              
              <p>Tell your name:</p>
              <p><input type='text' onChange={this.changeHandler} /></p>
              

            <label>
              <p>Select your course:</p>
              <select value={this.state.value} onChange={this.handleChange}>
                <option value="ASP.Net">ASP.Net</option>
                <option value="ReactJs">ReactJs</option>
                <option value="AngularJs">AngularJs</option>
                <option value="JavaScript">JavaScript</option>
              </select>
            </label>
            <p>Your Details:</p>
            <p><textarea value={ 'Your Name: '+this.state.participate +'\nYour Course: ' +this.state.value+'\nClick Submit to Register!!'}/></p>
            <p><input type="submit" value="Submit" /></p>
            
          </form>
          </Box>
          
          
          </div> 
         
          </body1>
          </div>
          <Footer/>
          </div>
          
         );
        }
      }
        

export default Register