import React, { Component } from 'react'
import Box from '@material-ui/core/Box';
import Header from './Header';
import Footer from './Footer';

export class Contactus extends Component {
    render() {
        return (
            <div>
              
              <Header/>
              <div class="body1">
              <body1>
                <div class="row">
                    <div class="col-md-12">

                   
                <u><h1>Contact us</h1></u>
                <div style={{ marginLeft: '10%', marginTop: '60px', width: '30%' }}>
    <Box color="white" bgcolor="black" p={1}>
        <p><u>Contact Details</u></p>
        <form>
  <div class="form-row">
    <div class="form-group col-md-12">
      <label for="Name1">Name:</label>
      <input type="Name" class="form-control" id="Name" placeholder="Name"/>
    </div>
    <div class="form-group col-md-12">
      <label for="Phone Number">Phone Number:</label>
      <p><input type="Phone Number" class="form-control" id="Phone Number" placeholder="Phone Number"/></p>
    </div>
  </div>
  
  <div class="form-row">
    <div class="form-group col-md-12">
      <label for="inputCity">City</label>
      <input type="text" class="form-control" id="inputCity"/>
    </div>
    <div class="form-group col-md-12">
      <label for="inputState">State</label>
      <select id="inputState" class="form-control">
        <option selected>Choose...</option>
        <option>Karnataka</option>
        <option>Gujrat</option>
        <option>Tamil Nadu</option>
        <option>Bihar</option>
      </select>
    </div>
   
  </div>
  <div class="form-group">
    <div class="form-check">
      <input class="form-check-input" type="checkbox" id="gridCheck"/>
      <label class="form-check-label" for="gridCheck">
        Confirm your Details
      </label>
    </div>
  </div>
  <button type="submit" class="btn btn-primary">Submit</button>
</form>

    </Box>
   
    
            </div>
            </div>
            <div class="col-md-12">
            
            <div style={{ marginLeft: '60%', marginTop: '-370px', width: '30%' }}>
    <Box color="white" bgcolor="black" p={1}>
        <p><u>Address of Office</u></p>
        <p>Manayata Tech Park,</p>
        <p>Nagavara, Bengaluru, Karnataka</p>
        <p>Pin code: 560054</p>
        <iframe title="React Map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3886.8135597626697!2d77.61776491404426!3d13.047536416739575!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bae177206475813%3A0x28695826eb487994!2sManyata%20Tech%20Park%20Rd%2C%20Bengaluru%2C%20Karnataka%20560045!5e0!3m2!1sen!2sin!4v1640284675203!5m2!1sen!2sin" width="200" height="150" style={{border:0}} allowfullscreen="" loading="lazy"></iframe>
        
        
    </Box>
    
    
    
    </div>

            </div>
            
            </div>
            </body1>
            </div>
            <Footer/>
                </div>
        )
    }
}

export default Contactus

